import amap from '../../common/amap-wx.js'; 
export default {
	data() {
		return {
			location:'',
			  amapPlugin: null,  
			  key: '4e1f4efe68009e524b688ee841b3a256',  
		};
	},
	methods:{
		 getRegeo() {  
		            uni.showLoading({  
		                title: '获取信息中'  
		            });  
		            this.amapPlugin.getRegeo({  
		                success: (data) => {  
		                    console.log(data)  
		                    this.location = data[0].name;  
		                    uni.hideLoading();  
		                }  
		            });
		},
		getMap(){
			uni.chooseLocation({
			    success:  res=> {
			        this.location = res.address
			    }
			});
		},
	},
	onLoad(){
		 this.amapPlugin = new amap.AMapWX({  
		      key: this.key  
		 });  
		 this.getRegeo()
	}
}
